<template>
    <div style="margin-left: 16px;">
      <p class="title"></p>
      <button @click="go"></button>
      <button @click="go1"></button>
      <p class="title"></p>
      <button @click="go2">/</button>
      <button @click="go3"></button>
    </div>
</template>
<script>
import mlAlert from "../vue-tool/packages/mlAlert";
export default {
  name: "Button",
  data() {
    return {};
  },
  methods: {
    go() {
      mlAlert("1", "1").then(res => {
        console.log(res);
      });
    },
    go1() {
      mlAlert({
        content: "",
        success: function(){
        
        }
      });
    },
    go3(){
      mlAlert({
        title:"",
        content: "",
        confirmButtonText:"yes",
        showCancelButton:false,
        success: function(){
        
        }
      });
    },
    go2(){
      mlAlert({
        title:"",
        content: "",
        cancelButtonText:"no",
        confirmButtonText:"yes",
        success: function(){
        
        }
      });
    }
  },
  components: {},
  mounted: function() {}
};
</script>
<style scoped>
.title{
    font-size: 14px;
    color: #999;
    margin-top: 14px;
    margin-bottom: 13px;
}
button{
      border: 1px solid #3d7bd4;
    color: #3d7bd4;
    padding: 7px 14px 7px 14px;
    background: white;
}
</style>



