var Regular = {
  // 
  mobile(phone) {
    var phone = phone
    if (!/^1[345678]\d{9}$/.test(phone)) {
      return false;
    } else {
      return true
    }
  },
  //   
  tel(tel) {
    var tel = tel
    if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(tel)) {
      return false;
    } else {
      return true
    }
  },
  //   Arr
  arrVacancy(arr) {
    var obj = Object.keys(arr)
    var s = true
    obj.forEach(res => {
      if (arr[res] === '' || arr[res] === undefined || arr[res] === null) {
        s = false
      }
    })
    return s
  },
  //   
  name(str) {
    var re1 = new RegExp("^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9])*$");
    if (!re1.test(str)) {
      return false;
    }
    return true;
  },
  // url
  getUrlParam(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    //
    var str = window.location.href.split("?")[1];
    if (str) {
      var r = str.substr(0).match(reg);
      //
      if (r != null)
        return decodeURI(r[2]);
      return '';
    } else {
      return ''
    }
  },
  // 
  symbol(str) {
    var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}' ]");
    if (pattern.test(str)) {
      return true;
    }
    return false;
  },
  // 
  figure(str) {
    var pattern = new RegExp("^[0-9]+\.{0,1}[0-9]{0,2}$");
    if (pattern.test(str)) {
      return true;
    }
    return false;
  },
  //   email 
  email(email) {
    var email = email
    if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(email)) {
      return false;
    } else {
      return true
    }
  },
  //   
  identity(identity) {
    // 1518151817X 
    var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
    if (reg.test(identity) === false) {
      return false;
    } else {
      return false
    }
  },
  //   msg   start   end  content
  star(msg, start = 3, end = msg.length, content = '*') {
    var msga = msg.replace(/(\d{3})(\d{4})/, "$1****$2");
    console.log(msga)
    return
  },
  //   url
  urlSplit(url) {
    var patt1 = /(\w+):\/\/([^/:]+)(:\d*)?([^# ]*)/;
    return url.match(patt1)
  }
}
export default Regular



