<template>
    <div style="width: 90%;margin: 0 auto;">
        <p class="title"></p>
        <button @click="go('')"></button>
        <button @click="go('')"></button>
        <p class="title"></p>
        <button @click="loading"></button>
        <p class="title">/</p>
        <button @click="success"></button>
        <button @click="fail"></button>
    </div>
</template>
<script>
import mlToast from "../vue-tool/packages/toast";
export default {
  name: "mlToast",
  data() {
    return {};
  },
  methods: {
    success(){
      mlToast({
        message: '',
        position: "middle",
        duration: 2000,
        icon: "success"
      });
    },
    loading(){
      mlToast({
        message: '',
        position: "middle",
        duration: 2000,
        icon: "loading"
      });
    },
    fail(){
      mlToast({
        message: '',
        position: "middle",
        duration: 2000,
        icon: "fail"
      });
    },
    go(s) {
      mlToast({
        message: s,
        position: "middle",
        duration: 2000,
        icon: ""
      });
    }
  },
  components: {},
  mounted: function() {
    
  }
};
</script>
<style scoped>
.title{
    font-size: 14px;
    color: #999;
    margin-top: 14px;
    margin-bottom: 13px;
}
button{
  color: #323233;
    background-color: #fff;
    border: 1px solid #ebedf0;
    padding: 10px 21px 10px 21px;
}
</style>



