import Vue from 'vue';
import {
  scroll,
  getScrollHeight,
  getWindowHeight
} from './scroll'

function InfiniteScroll() {}

/**
* infinite-scroll
* @module components/Infinite-scroll
* @desc 
* @param {fun} [infinite-scroll-disabled=0] -  [infinite-scroll]
* @param {fun} [infinite-scroll] -  callback
* @param {boolean} [only-once-teue] - infinite-scroll-disabled1px false  [self-adaption]
* @param {boolean} [self-adaption = false]  [infinite-scroll]
* @example
   
    //   // disabled = 
  <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="10">
    <li>1</li>
    <li>1</li>
    <li>1</li>
  </ul>
*/

function funs(el) {
  return new Promise(function (resolve, reject) {
    setTimeout(() => {
      resolve(document.body.offsetHeight - (el.offsetHeight + el.offsetTop))
    }, 500);
  });
}

function install() {
  Vue.directive('infinite-scroll', {
    bind: function (el, binding) {
      var disabled = el.getAttribute('infinite-scroll-disabled') || 0
      // infinite-scroll-disabled1px 
      var selfAdaption = el.getAttribute('self-adaption') || false
      var onlyOnce = el.getAttribute('only-once') || true
      if (selfAdaption) {
        Vue.prototype.$nextTick(function () {
          funs(el).then(res => {
            scroll(el, binding.value, Number(disabled) + Number(res), onlyOnce)
          })
        })
        return
      }
      scroll(el, binding.value, disabled, onlyOnce)
    },
    unbind: function () {
      window.onscroll = null
    }
  })
}
InfiniteScroll.install = install
if (!Vue.prototype.$isServer && window.Vue) {
  window.infiniteScroll = InfiniteScroll;
  Vue.use(install); // eslint-disable-line
}
InfiniteScroll.install()
export default InfiniteScroll



